<template>
  <div class="page-articles">
      <h1 style="font-size:.4rem">{{detail.title}}</h1>
      <p>{{detail.author}}</p>
      <div class="content" v-html="detail.content"></div>
      <div class="page">
        <OMButton
        @click="goto(obj.pre)"
        >
        <Icon value="left" />
            Ââç‰∏ÄÁØá
        </OMButton>
        <div class="mid">
            <span>{{obj.month}}</span>
            -
            <span>{{obj.num}}</span>
        </div>
        <OMButton
        @click="goto(obj.next)"
        >Âêé‰∏ÄÁØá
            <Icon value="right" />
        </OMButton>
      </div>
  </div>
</template>

<script>
import { getArticle } from '../api.config'

export default {
    name:'articles',
    data(){
      return {
        detail:{
          author:'',
          content:'',
          next:'',
          pre:'',
          title:'',
        }
      }
    },
    created(){
        let { id } = this.$route.params
        getArticle(id).then(res=> {
            this.detail = res.data
        })
    },
    watch:{
        '$route' (to, from) {
            if (to.params.id!== from.params.id) {
                let { id } = this.$route.params
                getArticle(id).then(res=> {
                    this.detail = res.data
                })
                window.document.scrollingElement.scrollIntoView()
            }
        }
    },
    computed:{
      obj(){
          const { month,next,num,pre  } = this.detail
          const data = {month,next,num,pre,type:'article'}
          return data
      }
    },
    methods:{
      goto(page){
          const { type } = this.obj
          if(page){
              this.$router.push({
                  name:type,
                  params:{id:page}
              })
          }else{
              this.$message('Ê≤°Êúâlaüçã')
          }
      }
    },
}
</script>

<style lang="stylus">
.page-articles
  padding .2rem
  .content
    margin-bottom 1rem
    position relative
    img 
      width 100%
.page
  width 100%
  position fixed
  bottom 0
  display flex
  justify-content space-between
  padding .2rem 
  .mid 
    line-height .71rem
    span 
      font-size .3rem
      line-height .71rem
</style>



